import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { UnloadingViewComponent } from './unloading-view.component';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
  {
    path: ':id',
    component: UnloadingViewComponent
  }
]

@NgModule({
  declarations: [UnloadingViewComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes)
  ]
})
export class UnloadingViewModule { }
