import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { UnloadService } from 'src/app/service/unload.service';
import { Location } from '@angular/common';
import { DataService } from 'src/app/service/data.service';

@Component({
  selector: 'app-unloading-view',
  templateUrl: './unloading-view.component.html'
})
export class UnloadingViewComponent implements OnInit {

  PageTitle = "Unloading View";
  viewId: string;
  objectArray: any;
  orderDetails: any = {};
  productList: Array<any> = [];
  currentCompany:any;
  date_format:string="M/d/yy";
  timezone:string
  time_format:string;
a="2020-04-13T07:00:46.213000";

  constructor(
    private activatedRoute: ActivatedRoute,
    private unloadService: UnloadService,
    private location: Location,
    private dataService:DataService
  ) {
    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.viewId = this.activatedRoute.snapshot.paramMap.get('id');
    }
  }

  ngOnInit() {
    this.dataService.currentCompany.subscribe((response)=>{
      if(response){     
      this.timezone=String(response.timezone.format);
      this.time_format=response.time_format.label;
      this.date_format=response.date_format.label;
      this.currentCompany=response;
      }
      
    });
    this.getObject();
  }

  back() {
    this.location.back();
  }

  getObject() {
    this.unloadService.getUnloadGeneratedViewById(this.viewId).subscribe((response) => {
      if (response.success) {
        this.orderDetails = response.data.order_detail;
        this.productList = response.data.product;
        // this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.orderDetails = {};
        this.productList = [];
      }
    }, (error) => {
      this.orderDetails = {};
      this.productList = [];

    });
  }

  print() {
    window.print();
  }

   convertUTCDateToLocalDate(date_string_you_received) {
     let date=new Date(date_string_you_received)
    let newDate = new Date(date.getTime()+date.getTimezoneOffset()*60*1000);
    let offset = date.getTimezoneOffset() / 60;
    let hours = date.getHours();
    newDate.setHours(hours - offset);
    return newDate;   
}

}
